import Facebook from '@static/icons/facebook.svg';
import Email from '@static/icons/email.svg';

const SOCIAL = [
    {
        icon: Facebook,
        link: '/',
    },
    {
        icon: Linkedin,
        link: '/',
    },
    {
        icon: Twitter,
        link: '/',
    },
    {
        icon: Email,
        link: '/contact/',
    },
];

const FooterWrapper = styled.footer`
    font-size: 12px;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 1px;
    color: var(--alt-color);
    background-color: var(--footer-color);

    a {
        color: #af944f;
    }
`;

