const SocialIcons = styled.div`
    img {
        width: 24px;
        height: 24px;
        margin-right: 16px;
    }

    svg {
        fill: #af944f !important;
    }
`;

const GRID = styled(Container)`
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @media (min-width: ${props => props.theme.screen.sm}) {
        flex-direction: row;
    }
`;

const StyledSection = styled(Section)`
    background: var(--footer-alt-color);
    padding: 3rem 0 !important;

    p {
        color: #6c757d;
    }

    .label {
        font-size: 14px;
        color: var(--accent-color);
    }

    ul {
        list-style-type: none;
        margin-left: 0;

        li {
            margin-bottom: 5px;
        }
    }

    svg {
        width: 50px;
    }
`;

export default function Footer(props) {
    return (
        <FooterWrapper {...props}>
            <StyledSection>
                <GRID>
                    <div>
                        <p className="label">Headquarters</p>
                        <p>Tollbugata 24, 0157, Oslo, Norway</p>
                        <p>
                            Los Angeles, CA
                            <br />
                            Warsaw, Poland
                        </p>
                    </div>
                    <div>
                        <p className="label">NFRONT</p>
                        <ul>
                            <li>
                                <Link to="/venture-capital/">
                                    Venture Capital
                                </Link>
                            </li>
                            <li>
                                <Link to="/outsource-development/">
                                    Outsourced Development
                                </Link>
                            </li>
                            <li>
                                <Link to="/hire-top-developer-teams/">
                                    Hire Top Developers
                                </Link>
                            </li>
                            <li>
                                <Link to="/corporate-innovation/">
                                    Corporate Innovation
                                </Link>
                            </li>
                        </ul>
                    </div>
                    <div>
                        <p className="label">Legal</p>
                        <ul>
                            <li>
                                <Link to="/">Privacy Policy</Link>
                            </li>
                            <li>
                                <Link to="/">GDPR</Link>
                            </li>
                        </ul>
                    </div>
                    <div>
                        <p className="label">Social</p>
                        <SocialIcons>
                            {SOCIAL.map(({ icon, link }) => (
                                <ExternalLink key={icon} href={link}>
                                    <img src={icon} alt="link" />
                                </ExternalLink>
                            ))}
                        </SocialIcons>
                    </div>
                </GRID>
            </StyledSection>
        </FooterWrapper>
    );
}

